<template>
  <div id="app" class="blackbg">
    <div
      v-if="showLoader"
      id="main-loader"
      class="blackbg"
      :class="loaded ? 'fadeout' : ''"
    >
      <div
        class="spinner-border text-secondary"
        style="width: 4rem; height: 4rem"
        role="status"
      ></div>
    </div>
    <div v-else id="main-workspace">
      <Header @document:new="newDocument" @svg:create="createSVG" />
      <Main
        ref="main"
        @tooltip:update="tooltipUpdate"
        @document:update="documentUpdate"
      />
      <Footer :tooltip="tooltip" />
    </div>
  </div>
</template>
<script>
import { computed } from "vue";
import Header from "./components/Header.vue";
import Main from "./components/Main.vue";
import Footer from "./components/Footer.vue";
import CanvasKitUtils from "./lib/canvaskit-utils.js";

export default {
  name: "App",
  mounted() {
    let el = this.$el;
    let loader = el.querySelector("#main-loader");
    if (loader) {
      loader.addEventListener("transitionend", () => {
        this.showLoader = false;
      });
      loader.addEventListener("animationend", () => {
        this.showLoader = false;
      });
    }
    // CanvasKit WASM
    CanvasKitUtils.initCanvasKit("lib/").then((canvaskit) => {
      this.loaded = true;
    });
  },
  methods: {
    documentUpdate(root) {
      this.root = root;
    },
    tooltipUpdate(event) {
      this.tooltip = event;
    },
    newDocument(event) {
      this.$refs["main"].createNewDocument(event);
    },
    createSVG(event) {
      this.$refs["main"].createSVG();
    },
  },

  data() {
    return {
      loaded: false,
      showLoader: true,
      tooltip: "",
      root: null,
    };
  },

  components: {
    Header,
    Main,
    Footer,
  },
};
</script>
<style>
#main-space {
  width: 100%;
  height: 100%;
  max-width: 100vw;
  max-height: 100vh;
  position: relative;
}

#main-workspace {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: whitesmoke;
  width: 100vw;
  height: 100vh;
  max-width: 100vw;
  max-height: 100vh;
  display: block;
}

#main-loader {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
