import ConerResizeTool from "./coner-resize-tool.js";

export default class NEResizeTool extends ConerResizeTool {
    constructor(f, m, b) {
        super(f, m, b, 'ne-resize');
    }

    updateTransformFeedback(feedback, dx, dy) {
        feedback.height -= dy;
        feedback.width += dx;
        feedback.y += dy * Math.cos(feedback.rotation);
        feedback.x -= dy * Math.sin(feedback.rotation);
    }
}