import CanvasKitUtils from "../canvaskit-utils.js";
import utils from "../canvaskit-utils.js"
import Figure from "../figure.js";
import u from "../utils.js";

export default class Shape extends Figure {
    constructor(props) {
        super(props);
        this._paint = utils.newPaint();
        this.setAntiAlias(true);

        this.r = props == null || props.r == null ? 255 : props.r;
        this.g = props == null || props.g == null ? 255 : props.g;
        this.b = props == null || props.b == null ? 255 : props.b;
        this.a = props == null || props.a == null ? 1 : props.a;

        this.br = props == null || props.br == null ? 0 : props.br;
        this.bg = props == null || props.bg == null ? 0 : props.bg;
        this.bb = props == null || props.bb == null ? 0 : props.bb;
        this.ba = props == null || props.ba == null ? 1 : props.ba;

        this.alpha = props == null || props.alpha == null ? 1 : props.alpha;
        this.fill = props == null || props.fill == null ? true : props.fill;
        this.showBorder = props == null || props.showBorder == null ? true : props.showBorder;
        this.borderWidth = props == null || props.borderWidth == null ? 1 : props.borderWidth;
        this.cap = props == null || props.cap == null ? CanvasKitUtils.StrokeCap.Butt : props.cap;
        this.join = props == null || props.join == null ? CanvasKitUtils.StrokeJoin.Bevel : props.join;
        this.miter = props == null || props.miter == null ? 2 : props.miter;
    }


    setAntiAlias(f) {
        if (!this._paint) return;
        this.getTarget()._paint.setAntiAlias(f);
    }

    get cap() {
        if (!this._paint) return;
        return this.getTarget()._paint.getStrokeCap();
    }

    set cap(c) {
        if (!this._paint) return;
        this.getTarget()._paint.setStrokeCap(c);
    }

    get join() {
        if (!this._paint) return;
        return this.getTarget()._paint.getStrokeJoin();
    }

    set join(c) {
        if (!this._paint) return;
        this.getTarget()._paint.setStrokeJoin(c);
    }

    get miter() {
        if (!this._paint) return;
        return this.getTarget()._paint.getStrokeMiter();
    }

    set miter(c) {
        if (!this._paint) return;
        this.getTarget()._paint.setStrokeMiter(c);
    }

    get borderWidth() {
        if (!this._paint) return;
        return this.getTarget()._paint.getStrokeWidth();
    }

    set borderWidth(w) {
        if (!this._paint) return;
        this.getTarget()._paint.setStrokeWidth(w);
    }

    get width() {
        return super.width;
    }

    set width(w) {
        if (w != this.width) {
            super.width = w;
            this.fireShapeDirty();
        }
    }

    get height() {
        return super.height;
    }

    set height(w) {
        if (w != this.height) {
            super.height = w;
            this.fireShapeDirty();
        }
    }

    deletePath() {
        if (this._path) {
            this.getTarget()._path.delete();
            this._path = null;
        }
    }

    fireShapeDirty() {
        this.deletePath();
        this.fireBoundsDirty();
        this.updateRelatedQuadTree();
        this.fireEvent('shapeDirty');
    }

    getPath() {
        if (!this._path) {
            this._path = utils.newPath();
            this.drawPath(this._path);
        }
        return this._path;
    }

    applyClip(ctx) {
        let path = this.getPath();
        if (path) ctx.clipPath(path, CanvasKitUtils.ClipOp.Intersect, true);
    }

    drawSelf(canvas) {
        if (this.alpha <= 0) return;
        let path = this.getPath();
        if (path) {
            if (this.a > 0 && this.fill) {
                this._paint.setStyle(utils.fillStyle);
                this._paint.setColorComponents(this.r / 255, this.g / 255, this.b / 255,
                    this.a * this.alpha);
                canvas.drawPath(path, this._paint);
            }
            if (this.showBorder && this.borderWidth > 0 && this.ba > 0) {
                this._paint.setStyle(utils.strokeStyle);
                this._paint.setColorComponents(this.br / 255, this.bg / 255, this.bb / 255,
                    this.ba * this.alpha);
                canvas.drawPath(path, this._paint);
            }
        }

        //debug: bounds
        // {
        // let b = this.bounds;
        // canvas.drawRect(CanvasKitUtils.newXYWHRect(b.left, b.top,
        //     b.right - b.left, b.bottom - b.top),
        //     this._paint);
        // }
    }

    drawPath(path) { }

    dispose() {
        if (this.isDisposed) return;
        this.deletePath();
        this.deletePaint();
        super.dispose();
    }

    deletePaint() {
        if (this._paint) {
            this.getTarget()._paint.delete();
            this._paint = null;
        }
    }

    getSVGExtensionString() {
        let str;
        if (this.fill) {
            str = `fill="${u.transformRGBToHex(this.r, this.g, this.b)}"`
            if (this.a != 1) {
                str += ` fill-opacity:"${this.a}"`;
            }
        } else {
            str = 'fill="none"';
        }

        if (this.showBorder) {
            if (!str) str = '';
            str += ` stroke="${u.transformRGBToHex(this.br, this.bg, this.bb)}"`
            str += ` stroke-width="${this.borderWidth}"`
            let join = utils.getJoinText(this.join);
            if (join) str += ` stroke-linejoin="${join}"`
            if (join == 'miter') str += ` stroke-miterlimit="${this.miter}"`
            let cap = utils.getCapText(this.cap);
            if (cap) str += ` stroke-linecap="${cap}"`
            if (this.ba != 1) {
                str += ` stroke-opacity="${this.ba}"`;
            }
        } else {
            if (!str) str = '';
            str += 'stroke="none"';
        }
        return str;
    }

}