import CenterRect from "../../lib/shape/center-rect.js";
import ShapeTool from "./shape-tool.js";

export default class RectTool extends ShapeTool {
    constructor(fr, mr, br, id = 'rect') {
        super(fr, mr, br, id);
    }
    createShape(x, y) {
        return new CenterRect({ name: 'Rectangle' });
    }
}