import Shape from "../../lib/shape/shape.js"
import Group from "../../lib/group.js";
import Point from "../../lib/point.js";

export default {
    emits: ['property:change'],
    methods: {
        firePropertyChangeEvents(events = []) {
            this.$emit('property:change', events);
        },

        canChangeProperty(model) {
            return true;
        },

        changeProperty(property, value) {
            if (!this.hasSelection) return;
            let events = [];
            this.selections.forEach(selection => {
                if (!this.canChangeProperty(selection)) return;
                events.push({ model: selection, property, value })
            });
            this.firePropertyChangeEvents(events);
        },

        changeMultipleProperties(properties) {
            if (!this.hasSelection) return;
            let events = [];
            this.selections.forEach(selection => {
                if (!this.canChangeProperty(selection)) return;
                for (let property in properties) {
                    let value = properties[property];
                    events.push({ model: selection, property, value })
                }
            });
            this.firePropertyChangeEvents(events);
        },
        getAnyPropertyValue(property, defaultValue) {
            if (!this.selections || this.selections.length == 0) return defaultValue;
            let v = this.selections[0][property];
            for (let index = 1; index < this.selections.length; index++) {
                const child = this.selections[index];
                if (child[property] != v) return defaultValue;
            }
            return v == null ? defaultValue : v;
        }
    },
    computed: {
        currentSelection() {
            if (this.selections) {
                return this.selections[0];
            }
        },

        hasSelection() {
            if (!this.selections || this.selections.length == 0) return false;
            return true;
        },

        hasBorderOrFillProperty() {
            if (this.hasSelection) {
                for (let index = 0; index < this.selections.length; index++) {
                    const selection = this.selections[index];
                    if (selection instanceof Point) return false;
                }
                return true
            }
            return false;
        },

        isShape() {
            if (this.selections == null || this.selections.length != 1) return false;
            let s = this.currentSelection;
            if (s instanceof Shape) {
                return true;
            }
            return false;
        },
        isGroup() {
            if (this.selections == null || this.selections.length != 1) return false;
            let s = this.currentSelection;
            if (s instanceof Group) {
                return true;
            }
            return false;
        }

    },
    props: {
        selections: {
            type: Array
        }
    }
}