import ShapeTool from "./shape-tool.js";
import CenterPolygon from "../../lib/shape/center-polygon.js";

export default class PolygonTool extends ShapeTool {
    constructor(fr, mr, br, id = 'polygon') {
        super(fr, mr, br, id);
    }

    createShape(x, y) {
        return new CenterPolygon({ name: 'Polygon' });
    }
}