import ConerResizeTool from "./coner-resize-tool.js";

export default class NWResizeTool extends ConerResizeTool {
    constructor(f, m, b) {
        super(f, m, b, 'nw-resize');
    }

    updateTransformFeedback(feedback, dx, dy, x, y) {
        feedback.height -= dy;
        feedback.width -= dx;

        feedback.y -= this.ly - y
        feedback.x -= this.lx - x
    }
}