import Shape from "./shape.js"
export default class Line extends Shape {
    constructor(props) {
        super(props);
        this.endX = props == null || props.endX == null ? this.x : props.endX;
        this.endY = props == null || props.endY == null ? this.y : props.endY;
    }

    isVisible() {
        return this.visible && (this.endX - this.x) != this.x && (this.endY - this.y) != this.y;
    }

    get showBorder() { return true };
    set showBorder(b) { }

    get fill() { return false; }
    set fill(f) { }

    get width() { }
    set width(w) { }
    get height() { }
    set height(h) { }

    get endX() { return this._endX }
    set endX(x) {
        if (x != this._endX) {
            this._endX = x;
            this.fireShapeDirty();
        }
    }

    get endY() { return this._endY }
    set endY(y) {
        if (y != this._endY) {
            this._endY = y;
            this.fireShapeDirty();
        }
    }

    calculateLocalBounds(b = { left: 0, top: 0, right: 0, bottom: 0 }) {
        b.left = Math.min(this.x, this.endX) - this.x;
        b.right = Math.max(this.x, this.endX) - this.x;
        b.top = Math.min(this.y, this.endY) - this.y;
        b.bottom = Math.max(this.y, this.endY) - this.y;
        let w = Math.abs(b.bottom - b.top);
        let h = Math.abs(b.right - b.left);
        if (w < 5) {
            let dw = 5 - w;
            b.left -= dw / 2;
            b.right += dw / 2;
        }
        if (h < 5) {
            let dh = 5 - h;
            b.top -= dh / 2;
            b.bottom += dh / 2;
        }
        return b;
    }

    drawPath(path) {
        path.moveTo(0, 0);
        path.lineTo(this.endX - this.x, this.endY - this.y);
    }

    getSVGLabelName() {
        return 'path';
    }

    getSVGDimensionString() { }

    getSVGExtensionString() {
        let ext = super.getSVGExtensionString();
        let path = this.getTarget().getPath();
        if (path) {
            if (!ext) ext = ''
            ext += ` d="${path.toSVGString()}"`;
        }
        return ext;
    }
}