const CURSOR = {
    default: 'default',
    select: 'default',
    move: "move",
    add: "crosshair",
    'e-resize': "e-resize",
    's-resize': "s-resize",
    'w-resize': "w-resize",
    'n-resize': "n-resize",
    'se-resize': "se-resize",
    'ne-resize': "ne-resize",
    'sw-resize': "sw-resize",
    'nw-resize': "nw-resize",
    rotate: 'url(/assets/images/cursor-rotate.png), auto',
    pen: "url(/assets/images/add-vertex.png), auto",
    deleteVertex: "url(/assets/images/add-vertex.png), auto",
    'vertex-select': 'url()',
    'move-root': 'grab'
};
import Rect from "./lib/shape/rect.js"
import Ellipse from "./lib/shape/ellipse.js"
import Star from "./lib/shape/star.js"
import Polygon from "./lib/shape/polygon.js"
import PathShape from "./lib/shape/path-shape"
import Group from "./lib/group.js"
import Point from "./lib/point.js";

export default class Utils {
    static calculateFiguresBounds(figures, useWorldMatrix = true) {
        if (!figures || figures.length == 0) return { left: 0, top: 0, right: 0, bottom: 0 };
        let tb = { left: Infinity, top: Infinity, right: -Infinity, bottom: -Infinity };
        figures.forEach(figure => {
            if (useWorldMatrix) {
                let wb = figure.worldBounds;
                tb.left = Math.min(wb.left, tb.left);
                tb.right = Math.max(wb.right, tb.right);
                tb.top = Math.min(wb.top, tb.top);
                tb.bottom = Math.max(wb.bottom, tb.bottom);
            } else {
                tb.left = Math.min(figure.x, tb.left);
                tb.right = Math.max(figure.x + figure.width, tb.right);
                tb.top = Math.min(figure.y, tb.top);
                tb.bottom = Math.max(figure.y + figure.height, tb.bottom);
            }
        })
        return tb;
    }

    static objectIcon(obj) {
        if (!obj) return;
        if (obj instanceof Rect) return "icon-square";
        if (obj instanceof Ellipse) return "icon-circle";
        if (obj instanceof Star) return "icon-star";
        if (obj instanceof Polygon) return "icon-polygon";
        if (obj instanceof PathShape) return "icon-circle";
        if (obj instanceof Group) return "icon-group";
        if (obj instanceof Point) return;
    }

    static CURSOR = CURSOR;
}