<template>
  <nav id="header" class="darkbg navbar navbar-expand-sm navbar-dark">
    <div class="container-fluid">
      <label class="navbar-brand"
        ><span style="font-weight: bold">MU</span
        ><span style="color: #aaaaaa">LIN</span></label
      >
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarNavDarkDropdown"
        aria-controls="navbarNavDarkDropdown"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNavDarkDropdown">
        <ul class="navbar-nav">
          <li class="nav-item dropdown">
            <a
              class="nav-link dropdown-toggle"
              id="navbarDarkDropdownMenuLink"
              role="button"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              File
            </a>
            <ul
              class="dropdown-menu"
              aria-labelledby="navbarDarkDropdownMenuLink"
            >
              <li>
                <a class="dropdown-item" @click="openNewDocDialog">New</a>
              </li>
              <li><a class="dropdown-item">Open</a></li>
              <li>
                <a class="dropdown-item" @click="generateSVG">Download SVG</a>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <new-doc-dialog :show="showDocDialog" @dialog:hidden="docDialogHidden" />
    </div>
  </nav>
</template>

<script>
import NewDocDialog from "./NewDocDialog.vue";
export default {
  name: "Header",
  components: { NewDocDialog },
  emits: ["document:new", "svg:create"],
  data() {
    return {
      showDocDialog: false,
    };
  },
  methods: {
    openNewDocDialog(e) {
      if (this.showDocDialog) return;
      this.showDocDialog = true;
    },
    docDialogHidden(event) {
      this.showDocDialog = false;
      if (event.type == "create") this.$emit("document:new", event);
    },
    generateSVG() {
      this.$emit("svg:create");
    },
  },
};
</script>

<style>
#header {
  padding: 2px 20px 2px 20px;
  box-sizing: border-box;
}
</style>