import ConerResizeTool from "./coner-resize-tool.js";

export default class SEResizeTool extends ConerResizeTool {
    constructor(f, m, b) {
        super(f, m, b, 'se-resize');
    }

    updateTransformFeedback(feedback, dx, dy) {
        feedback.height += dy;
        feedback.width += dx;
    }
}