import ConerResizeTool from "./coner-resize-tool.js";

export default class SWResizeTool extends ConerResizeTool {
    constructor(f, m, b) {
        super(f, m, b, 'sw-resize');
    }

    updateTransformFeedback(feedback, dx, dy) {
        feedback.height += dy;
        feedback.width -= dx;

        feedback.x += dx * Math.cos(feedback.rotation);
        feedback.y += dx * Math.sin(feedback.rotation);
    }
}