<template>
  <div id="editor-main">
    <div id="main-left" class="blackbg">
      <document :nodes="nodes" />
    </div>
    <div id="main-center">
      <Canvas
        ref="canvas"
        @selection:change="selectionChange"
        @tooltip:update="tooltipUpdate"
        @root:update="rootFigureUpdate"
      />
    </div>
    <div id="main-right">
      <PropertyBar
        :currentSelections="selections"
        @property:change="propertyChange"
      />
    </div>
  </div>
</template>

<script>
import ToolsBar from "./workspace/ToolsBar.vue";
import PropertyBar from "./property/PropertyBar.vue";
import Canvas from "./workspace/Canvas.vue";
import Document from "./structure/Document.vue";

export default {
  name: "Main",
  components: {
    ToolsBar,
    PropertyBar,
    Canvas,
    Document,
  },
  emits: ["tooltip:update"],

  mounted() {
    this.svgDownloadLink = document.createElement("a");
    let element = this.svgDownloadLink;
    element.style.display = "none";

    document.body.appendChild(element);
    // window.Split(["#main-center", "#main-right"], {
    //   sizes: [75, 25],
    // });
  },

  methods: {
    createSVG() {
      if (this.rootDocument) {
        this.rootDocument.getSVGString().then((svg) => {
          if (this.svgDownloadLink) {
            let link = this.svgDownloadLink;
            link.setAttribute(
              "href",
              "data:xml/plain;charset=utf-8," + encodeURIComponent(svg)
            );
            link.setAttribute("download", "untitle.svg");
            link.click();
          }
        });
      }
    },
    createNewDocument(event) {
      if (event.type != "create") return;
      this.$refs["canvas"].createNewMainRoot(event.width, event.height);
    },
    rootFigureUpdate(mainRoot) {
      this.rootDocument = mainRoot;
      // function generateModel(figure) {
      //   let m = {
      //     name: figure.name,
      //     id: figure.id,
      //   };
      //   if (figure.children && figure.children.length != 0) {
      //     m.children = [];
      //     figure.children.forEach((f) => {
      //       m.children.push(generateModel(f));
      //     });
      //     m.collapsed = figure.collapsed == null ? false : figure.collapased;
      //   }
      //   return m;
      // }
      // this.rootDocument = generateModel(mainRoot);
    },
    tooltipUpdate(event) {
      this.$emit("tooltip:update", event);
    },
    selectionChange(event) {
      // selectioncomponents
      this.selections = event.selections;
    },

    propertyChange(events) {
      this.$refs["canvas"].changeFigureProperty(events);
    },
  },

  computed: {
    nodes() {
      if (this.rootDocument) {
        return this.rootDocument.children;
      }
    },
  },

  data() {
    return {
      currentTool: "select",
      selections: [],
      rootDocument: null,
    };
  },
};
</script>

<style>
#editor-main {
  width: 100%;
  height: 100%;
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

#editor-main:focus {
  border: none;
}

#main-left {
  width: 14.6%;
  max-height: 100%;
  min-height: 100%;
  box-sizing: border-box;
}

#main-right {
  width: 14.6%;
}

#main-center {
  flex-grow: 1;
}

.gutter {
  background-color: #eee;
  background-repeat: no-repeat;
  background-position: 50%;
}

.gutter.gutter-horizontal {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAeCAYAAADkftS9AAAAIklEQVQoU2M4c+bMfxAGAgYYmwGrIIiDjrELjpo5aiZeMwF+yNnOs5KSvgAAAABJRU5ErkJggg==");
  cursor: col-resize;
}
</style>