import CompositeAction from "../../actions/compsite-action.js";
import ActionFactory from "../../actions/action-factory";
import Utils from "../../utils.js";
import Group from "../../lib/group.js";
export default {
    computed: {
    },
    methods: {
        unGroupFigures(selections = this.currentSelections) {
            if (!this.mainRoot) return;
            let action = new CompositeAction();
            selections.forEach((group) => {
                if (!(group instanceof Group)) return;
                let ga = new CompositeAction();
                let fs = group.children;
                fs.forEach((figure) => {
                    let m = figure.worldMatrix;
                    // mainrootfigure
                    let pm = this.mainRoot.worldMatrix.clone().invert();
                    let pose = pm.multiply(m).decompose();
                    let fa = new CompositeAction();
                    //   pose
                    let ra = ActionFactory.newRemoveFigureAction(figure, group);
                    fa.add(ra);
                    let aa = ActionFactory.newAddFigureAction(figure, this.mainRoot);
                    fa.add(aa);
                    let pa = ActionFactory.newPoseChangeAction([
                        {
                            figure: figure,
                            pose: pose,
                        },
                    ]);
                    fa.add(pa);
                    ga.add(fa);
                });
                let ra = ActionFactory.newRemoveFigureAction(group, this.mainRoot);
                ga.add(ra);
                action.add(ga);
            });

            this.actionStack.excute(action);
        },

        groupFigures(selections = this.currentSelections) {
            if (!this.mainRoot) return;
            let group = new Group({ name: "Group" });

            let figures = selections.map((v) => v);
            let bounds = Utils.calculateFiguresBounds(figures);
            let m = this.mainRoot.worldMatrix.clone().invert();
            let p1 = m.multiplyWithVertexDatas(bounds.left, bounds.top);
            let p2 = m.multiplyWithVertexDatas(bounds.right, bounds.bottom);
            group.x = p1[0];
            group.y = p1[1];
            group.width = p2[0] - p1[0];
            group.height = p2[1] - p1[1];
            let action = new CompositeAction();
            let add = ActionFactory.newAddFigureAction(group, this.mainRoot);
            action.add(add);

            figures.forEach((figure) => {
                let ca = new CompositeAction();
                let ta = ActionFactory.newTranslateAction(
                    [figure],
                    -group.x,
                    -group.y
                );
                ca.add(ta);
                if (figure.parent) {
                    let ra = ActionFactory.newRemoveFigureAction(figure, figure.parent);
                    ca.add(ra);
                }
                // Vueproxy
                let aa = ActionFactory.newAddFigureAction(figure.getTarget(), group);
                ca.add(aa);

                action.add(ca);
            });

            this.actionStack.excute(action);
            this.mainRoot.refresh();
        },
    },

}