import CompositeAction from "../../actions/compsite-action.js";
import ActionFactory from "../../actions/action-factory";
export default {
    methods: {
        clipFigures(event) {
            let mask = event.mask;
            let figures = event.figures;
            if (!mask || !figures || figures.length == 0 || !this.mainRoot) return;
            let m = mask.worldMatrix.invert();
            let actions = new CompositeAction();
            figures.forEach((f) => {
                let ca = new CompositeAction();
                let mf = f.worldMatrix;
                let fm = m.clone().multiply(mf);
                let ta = ActionFactory.newPoseChangeAction([
                    { figure: f, pose: fm.decompose() },
                ]);
                ca.add(ta);
                let ra = ActionFactory.newRemoveFigureAction(f, f.parent);
                ca.add(ra);
                let aa = ActionFactory.newAddFigureAction(f, mask);
                ca.add(aa);

                actions.add(ca);
            });
            if (!mask.clip) {
                actions.add(ActionFactory.newChangePropertyAction(mask, "clip", true));
            }
            if (mask.showBorder) {
                actions.add(
                    ActionFactory.newChangePropertyAction(mask, "showBorder", false)
                );
            }
            if (mask.fill) {
                actions.add(ActionFactory.newChangePropertyAction(mask, "fill", false));
            }

            this.actionStack.excute(actions);
            this.mainRoot.refresh();
        },

        unClipFigures(masks) {
            if (!masks || masks.length == 0 || !this.mainRoot) return;
            let action = new CompositeAction();
            masks.forEach((mask) => {
                let figures = mask.children;
                let mca = new CompositeAction();
                figures.forEach((figure) => {
                    let m = figure.worldMatrix;
                    let pm = this.mainRoot.worldMatrix.clone().invert();
                    let pose = pm.multiply(m).decompose();
                    let ca = new CompositeAction();

                    ca.add(ActionFactory.newRemoveFigureAction(figure, mask));
                    ca.add(ActionFactory.newAddFigureAction(figure, this.mainRoot));
                    ca.add(ActionFactory.newPoseChangeAction([{ figure, pose }]));

                    mca.add(ca);
                });

                if (mask.clip) {
                    mca.add(ActionFactory.newChangePropertyAction(mask, "clip", false));
                }
                if (!mask.showBorder) {
                    mca.add(
                        ActionFactory.newChangePropertyAction(mask, "showBorder", true)
                    );
                }
                if (!mask.fill) {
                    mca.add(ActionFactory.newChangePropertyAction(mask, "fill", true));
                }
                action.add(mca);
            });
            this.actionStack.excute(action);
            this.mainRoot.refresh();
        },
    },

}