<template>
  <div id="tool" class="darkbg">
    <div
      v-for="tool in tools"
      v-bind:key="tool.id"
      class="icon-container"
      :class="tool.split ? 'icon-spliter' : ''"
    >
      <i
        @click="click"
        :id="tool.id"
        class="iconfont"
        :class="tool.icon + ' ' + iconClass(tool.id)"
      ></i>
    </div>
  </div>
</template>

<script>
export default {
  name: "ToolsBar",
  emits: ["tool:update"],
  props: {
    currentTool: {
      type: String,
      default: null,
    },
    disabled: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      tools: [
        {
          id: "select",
          icon: "icon-mousepointer",
        },
        {
          id: "vertex-select",
          icon: "icon-vertexpointer",
          split: true,
        },
        {
          id: "pen",
          icon: "icon-pen",
          split: true,
        },
        {
          id: "line",
          icon: "icon-square",
        },
        {
          id: "rect",
          icon: "icon-square",
        },
        {
          id: "ellipse",
          icon: "icon-circle",
        },
        {
          id: "star",
          icon: "icon-star",
        },
        {
          id: "polygon",
          icon: "icon-polygon",
          split: true,
        },
      ],
    };
  },
  methods: {
    click(event) {
      if (this.disabled) return;
      let id = event.currentTarget.id;
      if (id) {
        if (this.currentTool != id) {
          this.$emit("tool:update", id);
        }
      }
    },

    iconClass(id) {
      if (this.disabled) return "deactive-icon";
      if (this.isActive(id)) {
        return "active-tool";
      } else {
        return "deactive-icon clickable-icon";
      }
    },

    isActive(id) {
      return id == this.currentTool && !this.disabled;
    },
  },
};
</script>

<style>
#tool {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 20px 10px 20px 10px;
  box-sizing: border-box;
  font-size: 1.5em;
}

.icon-container {
  width: 100%;
  padding: 0.5em 0.5em 0.5em 0.5em;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.deactive-icon {
  color: #777777;
}

.icon-spliter {
  border-bottom: #777777 solid 1px;
}

.active-tool {
  font-size: 1em;
  color: whitesmoke;
  cursor: pointer;
}

.clickable-icon {
  cursor: pointer;
  font-size: 1em;
}
</style>