<template>
  <div>
    <div v-if="!hasBorderOrFillProperty" style="text-algin: left; color: grey">
      <small>Nothing</small>
    </div>
    <div v-else>
      <div class="sub-property-row">
        <div class="single-row">
          <div class="form-check form-switch">
            <label for="showBorder">Show border</label>
            <input
              @change="showBorderChange"
              :checked="showBorder"
              class="form-check-input"
              type="checkbox"
              id="showBorder"
            />
          </div>
        </div>

        <div class="gray-splitter"></div>

        <div v-if="showBorder">
          <div class="property-row">
            <div class="property-item">
              <label class="property-lable">width</label>
              <input
                v-model.lazy="width"
                id="border-width"
                class="form-control form-control-sm property-input"
                type="number"
                @click="selectText"
                @keypress.enter="confirm"
              />
            </div>
            <div class="property-item">
              <label class="property-lable">miter</label>
              <input
                v-model.lazy="miter"
                id="border-miter"
                class="form-control form-control-sm property-input"
                type="number"
                @click="selectText"
                @keypress.enter="confirm"
              />
            </div>
          </div>

          <div class="sub-property-row">
            <div id="vertex-type-container">
              <div style="width: 100%; text-align: start; margin: 5px 0 0px 0">
                <div style="margin-bottom: 5px">
                  <small>Join</small>
                  <!-- <div
                    class="btn-group"
                    role="group"
                    aria-label="Basic radio toggle button group"
                  >
                    <input
                      type="radio"
                      class="btn-check"
                      name="btnradio"
                      id="btnradio1"
                      autocomplete="off"
                      checked
                    />
                    <label class="btn btn-outline-primary" for="btnradio1"
                      >Radio 1</label
                    >

                    <input
                      type="radio"
                      class="btn-check"
                      name="btnradio"
                      id="btnradio2"
                      autocomplete="off"
                    />
                    <label class="btn btn-outline-primary" for="btnradio2"
                      >Radio 2</label
                    >

                    <input
                      type="radio"
                      class="btn-check"
                      name="btnradio"
                      id="btnradio3"
                      autocomplete="off"
                    />
                    <label class="btn btn-outline-primary" for="btnradio3"
                      >Radio 3</label
                    >
                  </div> -->
                </div>
                <ul class="pagination pagination-sm justify-content-center">
                  <li
                    id="Bevel"
                    @click.stop="changeJoinType"
                    :class="join.value == 2 ? 'active' : ''"
                    class="page-item"
                  >
                    <label class="page-link">Bevel</label>
                  </li>
                  <li
                    id="Miter"
                    class="page-item"
                    :class="join.value == 0 ? 'active' : ''"
                    @click.stop="changeJoinType"
                  >
                    <label class="page-link">Miter</label>
                  </li>
                  <li
                    id="Round"
                    class="page-item"
                    :class="join.value == 1 ? 'active' : ''"
                    @click.stop="changeJoinType"
                  >
                    <label class="page-link">Round</label>
                  </li>
                </ul>
              </div>

              <div class="gray-splitter"></div>
            </div>
          </div>

          <div class="sub-property-row">
            <div id="vertex-type-container">
              <div style="width: 100%; text-align: start; margin: 5px 0 0px 0">
                <div style="margin-bottom: 5px">
                  <small>Cap</small>
                </div>
                <ul class="pagination pagination-sm justify-content-center">
                  <li
                    id="Butt"
                    @click.stop="changeCapType"
                    :class="cap.value == 0 ? 'active' : ''"
                    class="page-item"
                  >
                    <label class="page-link">Butt</label>
                  </li>
                  <li
                    id="Round"
                    class="page-item"
                    :class="cap.value == 1 ? 'active' : ''"
                    @click.stop="changeCapType"
                  >
                    <label class="page-link">Round</label>
                  </li>
                  <li
                    id="Square"
                    class="page-item"
                    :class="cap.value == 2 ? 'active' : ''"
                    @click.stop="changeCapType"
                  >
                    <label class="page-link">Square</label>
                  </li>
                </ul>
              </div>
            </div>
          </div>

          <div class="gray-splitter"></div>
          <div style="margin-top: 10px; text-align: left">
            <small>Color</small>
          </div>

          <div class="sub-property-row">
            <ColorProperty
              :selections="selections"
              @property:change="colorChanged"
              rk="br"
              gk="bg"
              bk="bb"
              ak="ba"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import textselect from "./text-click-fullselect.js";
import selectionTool from "./selection-tools";
import CanvasKitUtils from "../../lib/canvaskit-utils";
import Property from "./Property.vue";
import ColorProperty from "./ColorProperty.vue";
import Point from "../../lib/point.js";

export default {
  name: "BorderProperty",
  mixins: [textselect, selectionTool],
  components: { Property, ColorProperty },
  emits: ["property:change"],
  computed: {
    width: {
      get() {
        return this.getAnyPropertyValue("borderWidth", 0);
      },
      set(w) {
        let v = Number.parseInt(w);
        this.changeProperty("borderWidth", v);
      },
    },
    miter: {
      get() {
        return this.getAnyPropertyValue("miter", 0);
      },
      set(w) {
        let v = Number.parseInt(w);
        this.changeProperty("miter", v);
      },
    },
    join: {
      get() {
        return this.getAnyPropertyValue("join", CanvasKitUtils.StrokeCap.Bevel);
      },
    },
    cap: {
      get() {
        return this.getAnyPropertyValue("cap", CanvasKitUtils.StrokeCap.Butt);
      },
    },

    showBorder() {
      return this.getAnyPropertyValue("showBorder", false);
    },
  },
  methods: {
    canChangeProperty(model) {
      if (model instanceof Point) return false;
      return true;
    },
    colorChanged(events) {
      this.firePropertyChangeEvents(events);
    },
    changeCapType(event) {
      let str = event.currentTarget.id;
      if (!str) return;
      let c;
      if (str == "Butt") {
        c = CanvasKitUtils.StrokeCap.Butt;
      }
      if (str == "Square") {
        c = CanvasKitUtils.StrokeCap.Square;
      }
      if (str == "Round") {
        c = CanvasKitUtils.StrokeCap.Round;
      }
      if (!c || c == this.cap) return;
      this.changeProperty("cap", c);
    },

    changeJoinType(event) {
      let str = event.currentTarget.id;
      if (!str) return;
      let c;
      if (str == "Bevel") {
        c = CanvasKitUtils.StrokeJoin.Bevel;
      }
      if (str == "Miter") {
        c = CanvasKitUtils.StrokeJoin.Miter;
      }
      if (str == "Round") {
        c = CanvasKitUtils.StrokeJoin.Round;
      }
      if (!c || c == this.join) return;
      this.changeProperty("join", c);
    },
    showBorderChange(e) {
      let checked = e.target.checked;
      if (checked == this.showBorder) return;
      this.changeProperty("showBorder", checked);
    },
  },
  props: {
    selections: {
      type: Array,
    },
  },
};
</script>

