<template>
  <div>
    <div v-if="!hasBorderOrFillProperty" style="text-algin: left; color: grey">
      <small>Nothing</small>
    </div>
    <div v-else>
      <div class="sub-property-row">
        <div class="single-row">
          <div class="form-check form-switch">
            <label for="showBorder">Fill</label>
            <input
              @change="fillChange"
              :checked="canFill"
              class="form-check-input"
              type="checkbox"
              id="showBorder"
            />
          </div>
        </div>
        <div class="gray-splitter"></div>
        <div v-if="canFill">
          <div style="margin-top: 10px; text-align: left">
            <small>Color</small>
          </div>
          <color-property
            @property:change="firePropertyChangeEvents"
            :selections="selections"
            ak="a"
            rk="r"
            bk="b"
            gk="g"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import textselect from "./text-click-fullselect.js";
import selectionTool from "./selection-tools.js";
import ColorProperty from "./ColorProperty.vue";

export default {
  name: "FillProperty",
  mixins: [textselect, selectionTool],
  components: { ColorProperty },
  methods: {
    fillChange(event) {
      let checked = event.target.checked;
      if (this.canFill != checked) {
        this.changeProperty("fill", checked);
      }
    },
  },
  computed: {
    canFill() {
      return this.getAnyPropertyValue("fill", false);
    },
  },
};
</script>

