<template>
  <div class="property-item">
    <div class="property-lable" :class="disabled ? 'disabled-label' : ''">
      {{ name }}
    </div>
    <input
      v-if="disabled"
      class="form-control form-control-sm property-input"
      disabled
    />
    <input
      v-else
      class="form-control form-control-sm property-input"
      v-model.lazy="pvalue"
      :type="type"
      :step="step"
      :max="max"
      :min="min"
      @click="selectText"
      @keypress.enter="confirm"
    />
  </div>
</template>

<script>

export default {
  name: "Property",
  
  emits: ["property:change"],
  computed: {
    pvalue: {
      get() {
        if (this.disabled) return;
        let selection = this.selections[0];
        let value = selection[this.pk];
        if (value == null) return;
        if (this.transformToAngel) {
          let angle = (value * 180) / Math.PI;
          return angle.toFixed(this.fixed);
        }
        return value.toFixed(this.fixed);
      },

      set(v) {
        let value = v;
        if (this.type == "number") {
          value = Number.parseFloat(v);
        }
        if (this.transformToAngel) {
          value = (v * Math.PI) / 180;
        }
        this.$emit("property:change", [
          {
            model: this.selections[0],
            property: this.pk,
            value: value,
          },
        ]);
      },
    },
    disabled() {
      if (!(this.selections && this.selections.length == 1)) return true;
      let selection = this.selections[0];
      if (selection == null) return true;
      let flag = !this.pk in selection;
      // if (
      //   selection instanceof VerticesShape &&
      //   (this.pk == "width" || this.pk == "height")
      // )
      //   return true;
      return flag;
    },
  },

  methods: {
    confirm(event) {
      event.target.select();
      //   event.target.blur();
    },
    selectText(event) {
      event.target.select();
    },
  },

  props: {
    name: "",
    fixed: {
      type: Number,
      default: 2,
    },
    transformToAngel: {
      type: Boolean,
      default: false,
    },
    pk: {
      type: String,
    },
    selections: {
      type: Array,
    },
    step: {
      type: Number,
      default: 1,
    },
    max: {
      type: Number,
    },
    min: {
      type: Number,
    },
    type: {
      type: String,
      default: "number",
    },
  },
};
</script>

