<template>
  <div id="property-view" class="darkbg">
    <div id="selection-title">
      <i id="title-icon" class="iconfont" :class="titleIcon"></i>
      <strong>{{ title }}</strong>
    </div>
    <div id="properties-accordions-container">
      <div
        class="accordion accordion-flush properties-accordion-root"
        id="collapse-root"
      >
        <div class="accordion-item properties-accordion-item">
          <h2 class="accordion-header" id="basic-properties">
            <button
              id="accordion-button1"
              class="accordion-button properties-accordion-button"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#flush-collapseOne"
              aria-expanded="true"
              aria-controls="flush-collapseOne"
            >
              <i class="iconfont icon-position" style="margin-right: 5px"></i
              ><span>Transform</span>
            </button>
          </h2>
          <div
            id="flush-collapseOne"
            class="accordion-collapse collapse show"
            aria-labelledby="basic-properties"
            data-bs-parent="#collapse-root"
          >
            <div class="property-accordion-contents">
              <div v-if="canShow" class="property-row">
                <Property
                  name="x"
                  pk="x"
                  :fixed="0"
                  :selections="currentSelections"
                  @property:change="propertyChange"
                />
                <Property
                  name="y"
                  pk="y"
                  :fixed="0"
                  :selections="currentSelections"
                  @property:change="propertyChange"
                />
              </div>
              <div v-if="showDimension" class="property-row">
                <Property
                  name="width"
                  pk="width"
                  :fixed="0"
                  :selections="currentSelections"
                  @property:change="propertyChange"
                />
                <Property
                  name="height"
                  pk="height"
                  :fixed="0"
                  :selections="currentSelections"
                  @property:change="propertyChange"
                />
              </div>
              <div v-if="showScale" class="property-row">
                <Property
                  name="scaleX"
                  pk="scalex"
                  :step="0.1"
                  :selections="currentSelections"
                  @property:change="propertyChange"
                />
                <Property
                  name="scaleY"
                  pk="scaley"
                  :step="0.1"
                  :selections="currentSelections"
                  @property:change="propertyChange"
                />
              </div>
              <div v-if="showSkew" class="property-row">
                <Property
                  name="skewX"
                  pk="skewx"
                  :transformToAngel="true"
                  :selections="currentSelections"
                  @property:change="propertyChange"
                />
                <Property
                  name="skewY"
                  pk="skewy"
                  :transformToAngel="true"
                  :selections="currentSelections"
                  @property:change="propertyChange"
                />
              </div>
              <div v-if="showRotation" class="property-row">
                <Property
                  name="rotation"
                  pk="rotation"
                  :transformToAngel="true"
                  :selections="currentSelections"
                  @property:change="propertyChange"
                />
              </div>

              <!--  -->
              <div v-if="isRect" class="property-row">
                <Property
                  name="radiusX"
                  pk="radiusx"
                  :fixed="0"
                  :selections="currentSelections"
                  @property:change="propertyChange"
                />
                <Property
                  name="radiusY"
                  pk="radiusy"
                  :fixed="0"
                  :selections="currentSelections"
                  @property:change="propertyChange"
                />
              </div>
              <div v-if="isVertex" class="sub-property-row">
                <div id="vertex-type-container">
                  <div
                    style="width: 100%; text-align: start; margin: 5px 0 0px 0"
                  >
                    <div style="margin-bottom: 5px">
                      <small>Vertex Type</small>
                    </div>
                    <ul class="pagination pagination-sm justify-content-start">
                      <li
                        id="vertexType0"
                        @click.stop="changeVertexType"
                        :class="vertexType == 0 ? 'active' : ''"
                        class="page-item"
                      >
                        <label class="page-link"
                          ><i class="fas fa-wave-square"></i
                        ></label>
                      </li>
                      <li
                        id="vertexType1"
                        class="page-item"
                        :class="vertexType == 1 ? 'active' : ''"
                        @click.stop="changeVertexType"
                      >
                        <label class="page-link"
                          ><i class="fas fa-bezier-curve"></i
                        ></label>
                      </li>
                      <li
                        id="vertexType2"
                        class="page-item"
                        :class="vertexType == 2 ? 'active' : ''"
                        @click.stop="changeVertexType"
                      >
                        <label class="page-link"
                          ><i class="fas fa-code-branch"></i
                        ></label>
                      </li>
                    </ul>
                  </div>

                  <div
                    style="height: 1px; width: 100%; background-color: #444444"
                  ></div>
                </div>
              </div>

              <div v-if="isStar" class="sub-property-row">
                <div class="property-row">
                  <Property
                    name="corners"
                    pk="corners"
                    :fixed="0"
                    :selections="currentSelections"
                    @property:change="propertyChange"
                  />
                  <Property
                    name="inner"
                    pk="radius"
                    :fixed="2"
                    :step="0.1"
                    :selections="currentSelections"
                    @property:change="propertyChange"
                  />
                </div>
              </div>

              <div v-if="isPolygon" class="sub-property-row">
                <div class="property-row">
                  <Property
                    name="sides"
                    pk="sides"
                    :fixed="0"
                    :selections="currentSelections"
                    @property:change="propertyChange"
                  />
                </div>
              </div>

              <div v-if="isPath" class="sub-property-row">
                <div class="single-row">
                  <div class="form-check form-switch">
                    <label for="closePath">Closed</label>
                    <input
                      @change="pathCloseChange"
                      :checked="pathClosed"
                      class="form-check-input"
                      type="checkbox"
                      id="closePath"
                    />
                  </div>
                </div>
              </div>
              <div v-if="showIn" class="sub-property-row">
                <div class="single-row">
                  <div class="form-check form-switch">
                    <label for="inController">In Controller</label>
                    <input
                      @change="controllerChange"
                      :checked="hasInController"
                      class="form-check-input"
                      type="checkbox"
                      id="inController"
                    />
                  </div>
                </div>
                <div v-if="hasInController" class="property-row">
                  <Property
                    name="x"
                    pk="inX"
                    :fixed="0"
                    :selections="currentSelections"
                    @property:change="propertyChange"
                  />
                  <Property
                    name="y"
                    pk="inY"
                    :fixed="0"
                    :selections="currentSelections"
                    @property:change="propertyChange"
                  />
                </div>
              </div>

              <div v-if="showIn" class="sub-property-row">
                <div class="single-row">
                  <div class="form-check form-switch">
                    <label for="outController">Out Controller</label>
                    <input
                      @change="controllerChange"
                      :checked="hasOutController"
                      class="form-check-input"
                      type="checkbox"
                      id="outController"
                    />
                  </div>
                </div>
                <div v-if="hasOutController" class="property-row">
                  <Property
                    name="x"
                    pk="outX"
                    :fixed="0"
                    :selections="currentSelections"
                    @property:change="propertyChange"
                  />
                  <Property
                    name="y"
                    pk="outY"
                    :fixed="0"
                    :selections="currentSelections"
                    @property:change="propertyChange"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="accordion-item properties-accordion-item">
          <h2 class="accordion-header" id="border-property">
            <button
              class="accordion-button collapsed properties-accordion-button"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#flush-collapseTwo"
              aria-expanded="false"
              aria-controls="flush-collapseTwo"
            >
              <i class="iconfont icon-border" style="margin-right: 5px"></i>
              Border
            </button>
          </h2>
          <div
            id="flush-collapseTwo"
            class="accordion-collapse collapse"
            aria-labelledby="border-property"
            data-bs-parent="#collapse-root"
          >
            <div class="property-accordion-contents">
              <BorderProperty
                :selections="currentSelections"
                @property:change="propertyChange"
              />
            </div>
          </div>
        </div>
        <div class="accordion-item properties-accordion-item">
          <h2 class="accordion-header" id="flush-headingThree">
            <button
              class="accordion-button collapsed properties-accordion-button"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#flush-collapseThree"
              aria-expanded="false"
              aria-controls="flush-collapseThree"
            >
              <i class="iconfont icon-fill" style="margin-right: 5px"></i>
              Fill
            </button>
          </h2>
          <div
            id="flush-collapseThree"
            class="accordion-collapse collapse"
            aria-labelledby="flush-headingThree"
            data-bs-parent="#collapse-root"
          >
            <div class="property-accordion-contents">
              <fill-property
                :selections="currentSelections"
                @property:change="propertyChange"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BorderProperty from "./BorderProperty.vue";

import PathShape from "../../lib/shape/path-shape";
import Property from "./Property.vue";
import Point from "../../lib/point";
import Polygon from "../../lib/shape/polygon";
import Star from "../../lib/shape/star";
import FillProperty from "./FillProperty.vue";
import Rect from "../../lib/shape/rect";
import Utils from "../../utils";
import Line from "../../lib/shape/line";

export default {
  name: "PropertyBar",
  emits: ["property:change"],
  computed: {
    title() {
      if (!this.currentSelections || this.currentSelections.length == 0) {
        return "No Selections";
      } else {
        if (this.currentSelections) {
          if (this.currentSelections.length > 1) {
            return "Multiple Selections";
          } else {
            let selection = this.currentSelections[0];
            if (selection.name) return selection.name;
            if (selection instanceof Point) return "Vertex";
            return "Unknown";
          }
        }
      }
    },

    titleIcon() {
      if (!this.currentSelections || this.currentSelections.length == 0) {
      } else {
        if (this.currentSelections) {
          if (this.currentSelections.length > 1) {
            return "icon-shapes";
          } else {
            let selection = this.currentSelections[0];
            return Utils.objectIcon(selection);
          }
        }
      }
    },

    canShow() {
      return this.currentSelections && this.currentSelections.length == 1;
    },

    isVertex() {
      if (this.canShow) {
        return this.currentSelections[0] instanceof Point;
      }
      return false;
    },
    isRect() {
      if (this.canShow) {
        let m = this.currentSelections[0];
        return m instanceof Rect;
      }
      return false;
    },

    isStar() {
      if (this.canShow) {
        let m = this.currentSelections[0];
        return m instanceof Star;
      }
      return false;
    },

    isPolygon() {
      if (this.canShow) {
        let m = this.currentSelections[0];
        return m instanceof Polygon;
      }
      return false;
    },

    isPath() {
      if (this.canShow) {
        let m = this.currentSelections[0];
        if (m instanceof PathShape) {
          if (this.isStar || this.isPolygon) return false;
          return true;
        }
      }
      return false;
    },

    isLine() {
      if (this.canShow) {
        let m = this.currentSelections[0];
        if (m instanceof Line) {
          return true;
        }
      }
      return false;
    },

    showDimension() {
      if (this.canShow) {
        if (this.isVertex || this.isPath || this.isLine) return false;
        return true;
      }
      return false;
    },

    showScale() {
      if (this.canShow) {
        if (this.isVertex) return false;
        return true;
      }
      return false;
    },

    showSkew() {
      if (this.canShow) {
        if (this.isVertex) return false;
        return true;
      }
      return false;
    },

    showRotation() {
      if (this.canShow) {
        if (this.isVertex) return false;
        return true;
      }
      return false;
    },

    showIn() {
      return this.isVertex;
    },

    hasInController() {
      if (this.isVertex) {
        return this.currentSelections[0].in != null;
      }
      return false;
    },

    hasOutController() {
      if (this.isVertex) {
        return this.currentSelections[0].out != null;
      }
      return false;
    },

    pathClosed() {
      if (this.isPath) {
        return this.currentSelections[0].close;
      }
      return false;
    },

    vertexType() {
      if (this.isVertex) {
        return this.currentSelections[0].type;
      }
    },
  },
  methods: {
    propertyChange(event) {
      this.$emit("property:change", event);
    },
    changeVertexType(event) {
      let str = event.currentTarget.id;
      if (!str) return;
      let index = str.indexOf("vertexType");
      if (index == -1) return;
      let typeStr = str.substring(index + 10);
    },
    controllerChange(event) {
      let property = "in";
      if (event.target.id == "outController") property = "out";
      let model = this.currentSelections[0];
      let checked = event.target.checked;
      let value = null;
      if (checked) {
        value = [model.x, model.y];
      }
      let e = { model, property, value };
      this.propertyChange([e]);
    },
    pathCloseChange(event) {
      let checked = event.target.checked;
      this.propertyChange([
        { model: this.currentSelections[0], property: "close", value: checked },
      ]);
    },
  },
  components: { Property, BorderProperty, FillProperty },
  props: {
    currentSelections: {
      type: Array,
      default: null,
    },
  },
};
</script>

<style>
#vertex-type-container {
  width: 100%;
  display: flex;
  justify-content: baseline;
  align-items: center;
  flex-direction: column;
}

.sub-property-row {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.single-row {
  padding: 5px 0 0 0;
  box-sizing: border-box;
}

#title-icon {
  margin-left: 10px;
  margin-right: 15px;
}
#selection-title {
  width: 100%;
  text-align: start;
  font-size: 1em;
  padding-bottom: 5px;
  box-sizing: border-box;
  border-bottom: #777777 solid 1px;
}

#property-view {
  height: 100%;
  max-height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 20px;
  box-sizing: border-box;
  color: whitesmoke;
}

.properties-accordion-root {
  background-color: transparent;
}

#properties-accordions-container {
  flex-grow: 1;
  width: 100%;
  padding-top: 10px;
  max-height: 100%;
  box-sizing: border-box;
  background-color: transparent;
}

#collapse-root {
  height: 100%;
}

#properties-accordion-root {
  height: 100%;
}

.properties-accordion-body {
  display: flex;
  flex-direction: column;
}

.property-item {
  display: flex;
  color: #dddddd;
  justify-content: center;
  width: 100%;
  align-items: baseline;
}

.property-lable {
  margin-right: 5px;
}

.property-input {
  border: none;
  background-color: transparent;
  color: #dddddd;
  border-bottom: #444444 solid 1px;
  border-radius: 0;
}

.property-input:focus {
  color: whitesmoke;
  background-color: transparent;
  box-shadow: none;
  border-bottom: whitesmoke solid 1px;
  border-radius: 0;
}

.property-row {
  display: flex;
  flex: 50% 50%;
  justify-content: space-between;
  box-sizing: border-box;
}

.property-accordion-contents {
  padding: 10px 0 10px 0;
  box-sizing: border-box;
  color: #dddddd;
}

.properties-accordion-item {
  background-color: transparent;
}

.properties-accordion-button {
  background-color: #313131;
  color: whitesmoke;
}

.properties-accordion-button:focus {
  box-shadow: none;
  color: whitesmoke;
}
.properties-accordion-button:not(.collapsed) {
  color: whitesmoke;
  background-color: #313131;
}

.properties-accordion-button::after {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23ffffff'%3E%3Cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 01.708 0L8 10.293l5.646-5.647a.5.5 0 01.708.708l-6 6a.5.5 0 01-.708 0l-6-6a.5.5 0 010-.708z'/%3E%3C/svg%3E");
}

.properties-accordion-button:not(.collapsed):after {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23ffffff'%3E%3Cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 01.708 0L8 10.293l5.646-5.647a.5.5 0 01.708.708l-6 6a.5.5 0 01-.708 0l-6-6a.5.5 0 010-.708z'/%3E%3C/svg%3E");
  transform: rotate(180deg);
}

.properties-accordion-body {
  padding: 5px 0px 5px 0px;
  box-sizing: border-box;
}
</style>