import CenterEllipse from "../../lib/shape/center-ellipse.js";
import ShapeTool from "./shape-tool.js";

export default class EllipseTool extends ShapeTool {
    constructor(fr, mr, br, id = 'ellipse') {
        super(fr, mr, br, id);
    }

    createShape(x, y) {
        return new CenterEllipse({ name: 'Ellipse' });
    }
}