export default {
    methods: {
        keydown(event) {
            if (event.code == 'Delete') {
                if (this._deleteAction) {
                    cancelAnimationFrame(this._deleteAction);
                }
                this._deleteAction = requestAnimationFrame(() => {
                    this.deleteActionFired();
                })
            }
            if (event.code == 'Space') {
                this.spacePressed = true;
            }
        },
        keyup(event) {
            if (event.code == 'Space') {
                this.spacePressed = false;
            }
        },

        keypress(event) {
            if (this._keyPressId) {
                cancelAnimationFrame(this._keyPressId);
            }
            this._keyPressId = requestAnimationFrame(() => {
                if (event.code == 'KeyZ') {
                    if (event.ctrlKey && !event.shiftKey) {
                        this.undoActionFired();
                        return;
                    }
                    if (event.ctrlKey && event.shiftKey) {
                        this.redoActionFired();
                        return;
                    }
                }

                if (event.code == 'KeyV') {
                    this.currentTool = 'select'
                }
                if (event.code == 'KeyA') {
                    this.currentTool = 'vertex-select'
                }
                if (event.code == 'KeyW') {
                    this.currentTool = 'pen'
                }
                if (event.code == 'KeyG') {
                    if (event.ctrlKey && !event.shiftKey) {
                        this.groupActionFired();
                        return;
                    }
                    if (event.ctrlKey && event.shiftKey) {
                        this.ungroupActionFired();
                        return;
                    }
                }
            })
        },

        groupActionFired() {
            let actionbar = this.$refs["actionBar"];
            if (actionbar) actionbar.groupFigures();
        },

        ungroupActionFired() {
            let actionbar = this.$refs["actionBar"];
            if (actionbar) actionbar.unGroup();
        },

        deleteActionFired() {
            let actionbar = this.$refs["actionBar"];
            if (actionbar) actionbar.deleteSelections();
        },
        redoActionFired() {
            let actionbar = this.$refs["actionBar"];
            if (actionbar) actionbar.redo();
        },
        undoActionFired() {
            let actionbar = this.$refs["actionBar"];
            if (actionbar) actionbar.undo();
        }
    },
    mounted() {
        window.addEventListener("keydown", this.keydown);
        window.addEventListener("keyup", this.keyup);
        window.addEventListener('keypress', this.keypress);
    },
    data() {
        return {
            spacePressed: false
        }
    }
}