<template>
  <div
    class="modal fade"
    id="new-doc-dialog"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered doc-root">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="staticBackdropLabel">
            <i class="iconfont icon-document"></i>
            New Document
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="container">
            <div class="row">
              <select
                class="form-select"
                aria-label="Default select example"
                v-model="selectedTemplate"
              >
                <option
                  v-for="template in sizeTemplate"
                  v-bind:key="template.name"
                >
                  {{ template.name }}
                </option>
              </select>
            </div>
            <div class="row" style="margin-top: 20px">
              <div class="col">
                <div class="input-group">
                  <span class="input-group-text">Width</span>
                  <input
                    v-model="width"
                    type="number"
                    class="form-control"
                    aria-label="Amount (to the nearest dollar)"
                  />
                  <span class="input-group-text">px</span>
                </div>
              </div>
              <div class="col">
                <div class="input-group">
                  <span class="input-group-text">Height</span>
                  <input
                    v-model="height"
                    type="number"
                    class="form-control"
                    aria-label="Amount (to the nearest dollar)"
                  />
                  <span class="input-group-text">px</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            Close
          </button>
          <button type="button" class="btn btn-primary" @click="createDocument">
            Create
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "NewDocumentDialog",
  emits: ["dialog:hidden", "dialog:shown"],
  mounted() {
    let docElement = document.getElementById("new-doc-dialog");
    if (!docElement) return;
    docElement.addEventListener("hidden.bs.modal", (event) => {
      this.$emit("dialog:hidden", {
        type: this.type,
        width: Number.parseFloat(this.width),
        height: Number.parseFloat(this.height),
      });
    });
    docElement.addEventListener("shown.bs.modal", (event) => {
      this.$emit("dialog:shown");
    });
    this.dialog = new bootstrap.Modal(docElement);
  },
  methods: {
    createDocument() {
      this.type = "create";
      this.dialog.hide();
    },
  },
  data() {
    return {
      dialog: null,
      width: 300,
      height: 300,
      type: "cancel",
      sizeTemplate: [
        { name: "Custom", width: 300, height: 300 },
        { name: "Letter", width: 612, height: 792 },
        { name: "A4", width: 595.28, height: 841.89 },
        { name: "A3", width: 841.89, height: 1190.55 },
        { name: "B5", width: 515.91, height: 728.5 },
        { name: "B4", width: 728.5, height: 1031.81 },
      ],
      selectedTemplate: "Custom",
    };
  },
  watch: {
    show(c, old) {
      if (!this.dialog) return;
      if (c) {
        this.type = "cancel";
        this.dialog.show();
      } else {
        this.dialog.hide();
      }
    },

    selectedTemplate(c, old) {
      this.sizeTemplate.forEach((p) => {
        if (p.name == c) {
          this.width = p.width;
          this.height = p.height;
        }
      });
    },
  },
  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },
};
</script>

<style>
.doc-root {
  color: #333333;
}
</style>