import Vector2 from "../../lib/math/vector2.js";
import Utils from "../../lib/utils.js";
import PoseChangeTool from "./pose-change-tool.js";

export default class NResizeTool extends PoseChangeTool {
    constructor(f, n, b) {
        super(f, n, b, 'n-resize');
    }

    get isIllegalData() {
        return this.ey - this.sy == 0;
    }

    activeMove(x, y, l, t, r, b, dx, dy) {
        super.activeMove(x, y, l, t, r, b, dx, dy);
        if (!this.transformFeedback) return;
        let dis = Utils.distance(this.lx, this.ly, x, y);
        if (dy == 0) dis = 0;
        if (dy > 0) dis *= -1;
        this.transformFeedback.height += dis;
        let v = new Vector2(0, -1).rotate(this.transformFeedback.rotation);
        v.scale(dis);
        this.transformFeedback.y += v.y;
        this.transformFeedback.x += v.x;
        this.frontRoot.refresh();
    }
}