<template>
  <div class="canvas-action-bar graybg">
    <div class="action-bar-main">
      <div class="action-container">
        <button
          type="button"
          class="btn btn-primary action-button"
          @click="deleteSelections"
          :class="hasSelection ? '' : 'disabled'"
        >
          <i class="iconfont icon-trash"></i>
        </button>
      </div>

      <div class="action-container"><div class="action-spliter"></div></div>

      <div class="action-container">
        <button
          @click="undo"
          type="button"
          class="btn btn-primary action-button"
          :class="canUndo ? '' : 'disabled'"
        >
          <i class="iconfont icon-undo"></i>
        </button>
      </div>
      <div class="action-container">
        <button
          @click="redo"
          type="button"
          class="btn btn-primary action-button"
          :class="canRedo ? '' : 'disabled'"
        >
          <i class="iconfont icon-redo"></i>
        </button>
      </div>

      <div class="action-container"><div class="action-spliter"></div></div>

      <div class="action-container">
        <button
          @click="groupFigures"
          type="button"
          class="btn btn-primary action-button"
          :class="canGroup ? '' : 'disabled'"
        >
          <i class="iconfont icon-group"></i>
        </button>
      </div>
      <div class="action-container">
        <button
          @click="unGroup"
          type="button"
          class="btn btn-primary action-button"
          :class="canUnGroup ? '' : 'disabled'"
        >
          <i class="iconfont icon-ungroup"></i>
        </button>
      </div>

      <div class="action-container"><div class="action-spliter"></div></div>

      <div class="action-container">
        <button
          @click="clip"
          type="button"
          class="btn btn-primary action-button"
          :class="canClip ? '' : 'disabled'"
        >
          <i class="iconfont icon-mask-clip larger-action-icon"></i>
        </button>
        <button
          @click="unClip"
          type="button"
          class="btn btn-primary action-button"
          :class="canUnClip ? '' : 'disabled'"
        >
          <i class="iconfont icon-mask-unclip larger-action-icon"></i>
        </button>
      </div>

      <div class="action-container"><div class="action-spliter"></div></div>

      <div class="action-container">
        <button
          @click="bringFront"
          type="button"
          class="btn btn-primary action-button"
          :class="canBringFront ? '' : 'disabled'"
        >
          <i class="iconfont icon-bring-front larger-action-icon"></i>
        </button>
        <button
          @click="sendBack"
          type="button"
          class="btn btn-primary action-button"
          :class="canSendBack ? '' : 'disabled'"
        >
          <i class="iconfont icon-send-back larger-action-icon"></i>
        </button>
      </div>

      <div class="action-container"><div class="action-spliter"></div></div>

      <div class="action-container">
        <button
          @click="alignLeft"
          type="button"
          class="btn btn-primary action-button"
          :class="canAlign ? '' : 'disabled'"
        >
          <i class="iconfont icon-align-left more-larger-action-icon"></i>
        </button>
        <button
          @click="alignCenter(true, 'Align Center')"
          type="button"
          class="btn btn-primary action-button"
          :class="canAlign ? '' : 'disabled'"
        >
          <i class="iconfont icon-align-center more-larger-action-icon"></i>
        </button>
        <button
          @click="alignRight"
          type="button"
          class="btn btn-primary action-button"
          :class="canAlign ? '' : 'disabled'"
        >
          <i class="iconfont icon-align-right more-larger-action-icon"></i>
        </button>
        <button
          @click="alignTop"
          type="button"
          class="btn btn-primary action-button"
          :class="canAlign ? '' : 'disabled'"
        >
          <i class="iconfont icon-align-top more-larger-action-icon"></i>
        </button>
        <button
          @click="alignCenter(false, 'Align V-Center')"
          type="button"
          class="btn btn-primary action-button"
          :class="canAlign ? '' : 'disabled'"
        >
          <i class="iconfont icon-align-v-center more-larger-action-icon"></i>
        </button>

        <button
          @click="alignBottom"
          type="button"
          class="btn btn-primary action-button"
          :class="canAlign ? '' : 'disabled'"
        >
          <i class="iconfont icon-align-bottom more-larger-action-icon"></i>
        </button>
      </div>

      <div class="action-container"><div class="action-spliter"></div></div>

      <div class="action-container">
        <button
          @click="distribute('left', true)"
          type="button"
          class="btn btn-primary action-button"
          :class="canDistribute ? '' : 'disabled'"
        >
          <i class="iconfont icon-distribute-left more-larger-action-icon"></i>
        </button>
        <button
          @click="distribute('center', true)"
          type="button"
          class="btn btn-primary action-button"
          :class="canDistribute ? '' : 'disabled'"
        >
          <i
            class="iconfont icon-distribute-center more-larger-action-icon"
          ></i>
        </button>
        <button
          @click="distribute('right', true)"
          type="button"
          class="btn btn-primary action-button"
          :class="canDistribute ? '' : 'disabled'"
        >
          <i class="iconfont icon-distribute-right more-larger-action-icon"></i>
        </button>
        <button
          @click="distribute('top', false)"
          type="button"
          class="btn btn-primary action-button"
          :class="canDistribute ? '' : 'disabled'"
        >
          <i class="iconfont icon-distribute-top more-larger-action-icon"></i>
        </button>
        <button
          @click="distribute('center', false)"
          type="button"
          class="btn btn-primary action-button"
          :class="canDistribute ? '' : 'disabled'"
        >
          <i
            class="iconfont icon-distribute-v-center more-larger-action-icon"
          ></i>
        </button>

        <button
          @click="distribute('bottom', false)"
          type="button"
          class="btn btn-primary action-button"
          :class="canDistribute ? '' : 'disabled'"
        >
          <i
            class="iconfont icon-distribute-bottom more-larger-action-icon"
          ></i>
        </button>
      </div>
    </div>
    <div class="action-bar-other">
      <input
        v-show="hasRootFigure"
        class="form-control form-control-sm property-input"
        v-model.lazy="mainScale"
        @click="selectText"
        @keypress.enter="confirm"
        style="width: 6em; text-align: center"
      />
    </div>
  </div>
</template>

<script>
import ActionFactory from "../../actions/action-factory";
import CompositeAction from "../../actions/compsite-action";
import RemoveFigureAction from "../../actions/remove-figure-action";
import Figure from "../../lib/figure";
import Group from "../../lib/group";
import Point from "../../lib/point";
import Shape from "../../lib/shape/shape";
import VerticesShape from "../../lib/shape/vertices-shape";
import textselect from "../property/text-click-fullselect.js";
import changeindex from "./change-index-processor.js";
import alignprocessor from "./align-processor.js";
import distributeprocessor from "./distribute-processor";

export default {
  name: "ActionBar",
  mixins: [textselect, changeindex, alignprocessor, distributeprocessor],
  emits: [
    "canvas:refresh",
    "canvas:group",
    "canvas:ungroup",
    "canvas:clip",
    "canvas:unclip",
  ],

  computed: {
    hasRootFigure() {
      let r = this.rootFigure != null;
      return r;
    },
    mainScale: {
      get() {
        if (this.rootFigure) {
          let zoom = this.rootFigure.zoom;
          let r = (zoom * 100).toFixed(2) + "%";
          return r;
        }
      },
      set(v) {
        if (this.rootFigure) {
          let z = Number.parseFloat(v);
          if (isNaN(z)) return;
          this.rootFigure.zoom = z / 100;
          this.rootFigure.placeAtCenter();
          this.rootFigure.refresh();
        }
      },
    },
    canRedo() {
      if (this.actionStack) return this.actionStack.canRedo();
    },
    canUndo() {
      if (this.actionStack) return this.actionStack.canUndo();
    },
    hasSelection() {
      if (!this.selections) return;
      return this.selections.length > 0;
    },
    canGroup() {
      if (!this.selections) return false;
      return this.selections.length > 1;
    },
    canUnGroup() {
      if (!this.selections) return false;
      if (this.selections.length == 0) return false;
      for (let i = 0; i < this.selections.length; i++) {
        const selection = this.selections[i];
        if (!(selection instanceof Group)) {
          return false;
        }
      }
      return true;
    },

    clipMask() {
      if (this.selections && this.selections.length > 1) {
        let index = -1;
        let mask;
        for (let i = 0; i < this.selections.length; i++) {
          const selection = this.selections[i];
          let sp = selection.parent;
          if (sp) {
            let si = sp.indexOfChild(selection);
            if (si > index) {
              index = si;
              mask = selection;
            }
          }
        }
        if (mask instanceof Shape) return mask;
      }
    },

    canClip() {
      return this.clipMask != null;
    },
    canUnClip() {
      if (this.selections) {
        //
        for (let i = 0; i < this.selections.length; i++) {
          const selection = this.selections[i];
          if (selection.clip && selection.children.length >= 1) return true;
        }
      }
      return false;
    },
  },

  methods: {
    clip() {
      let mask = this.clipMask;
      let figures = [];
      this.selections.forEach((s) => {
        if (mask != s) figures.push(s);
      });
      // 
      figures.sort((a, b) => {
        let ep = a.parent;
        let a1 = ep.indexOfChild(a);
        ep = b.parent;
        let b1 = ep.indexOfChild(b);
        return a1 - b1;
      });
      this.$emit("canvas:clip", { mask: mask, figures: figures });
    },
    unClip() {
      if (!this.canUnClip) return;
      let masks = [];
      this.selections.forEach((selection) => {
        if (selection.clip && selection.children.length >= 1) {
          masks.push(selection);
        }
      });
      this.$emit("canvas:unclip", masks);
    },
    unGroup() {
      if (this.canUnGroup) {
        this.$emit("canvas:ungroup", this.selections);
      }
    },
    groupFigures(event) {
      if (!this.canGroup) return;
      this.$emit("canvas:group", this.selections);
    },

    deleteSelections(event) {
      if (!this.hasSelection || !this.actionStack) return;
      let composite = new CompositeAction();

      this.selections.forEach((selection) => {
        if (selection instanceof Figure) {
          let action = new RemoveFigureAction(selection, selection.parent);
          composite.add(action);
        }
        if (selection instanceof Point) {
          let parent = selection.parent;
          while (true) {
            if (parent == null || parent instanceof VerticesShape) {
              break;
            }
            parent = parent.parent;
          }
          if (parent) {
            let index = parent.getPathModel().points.indexOf(selection);
            if (index != -1) {
              let action = new ActionFactory.newDeletePointAction(
                parent,
                index
              );
              composite.add(action);
            }
          }
        }
      });
      this.actionStack.excute(composite);
    },

    undo() {
      if (this.canUndo) {
        this.actionStack.undo();
      }
    },

    redo() {
      if (this.canRedo) {
        this.actionStack.redo();
      }
    },

    triggerCanvasRefresh() {
      this.$emit("canvas:refresh");
    },
  },

  props: {
    actionStack: null,
    selections: null,
    rootFigure: null,
  },
};
</script>

<style>
.canvas-action-bar {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  padding: 2px 10px 2px 10px;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  border-bottom: #777777 solid 1px;
}

.action-bar-main {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.action-spliter {
  width: 1px;
  height: 1.5em;
  background-color: #777777;
}

.action-container {
  padding: 0 5px 0 5px;
  box-sizing: border-box;
}
.more-larger-action-container {
  /* padding: 0 5px 0 5px; */
  box-sizing: border-box;
  font-size: 2em;
}

.action-button {
  padding: 2px;
  border: none;
  box-shadow: none;
  background-color: transparent;
  color: #dddddd;
}

.action-button:focus {
  background-color: transparent;
  border: none;
  box-shadow: none;
}

.btn-primary.disabled,
.btn-primary:disabled {
  background-color: transparent;
  border-color: transparent;
  color: #313131;
}

.action-button:hover {
  background-color: transparent;
  border: none;
  box-shadow: none;
}

.larger-action-icon {
  font-size: 1.2em;
}

.more-larger-action-icon {
  font-size: 1.5em;
}
</style>