import ActionFactory from "../../actions/action-factory.js";
import Figure from "../../lib/figure.js";

export default {
    methods: {
        bringFront() {
            if (!this.selections) return;
            let s = this.selections[0];
            let index = this.rootFigure.indexOfChild(s);
            index += 1;
            let action = ActionFactory.newChangeIndexAction(s, index);
            this.actionStack.excute(action);
        },
        sendBack() {
            if (!this.selections) return;
            let s = this.selections[0];
            let index = this.rootFigure.indexOfChild(s);
            index -= 1;
            let action = ActionFactory.newChangeIndexAction(s, index);
            this.actionStack.excute(action);
        },
    },
    computed: {
        canBringFront() {
            if (this.selections && this.selections.length == 1) {
                let s = this.selections[0];
                if (s instanceof Figure) {
                    if (
                        this.rootFigure.children[this.rootFigure.children.length - 1] != s
                    )
                        return true;
                }
            }
            return false;
        },
        canSendBack() {
            if (this.selections && this.selections.length == 1) {
                let s = this.selections[0];
                if (s instanceof Figure) {
                    if (this.rootFigure.children[0] != s) return true;
                }
            }
            return false;
        },
    }
}
