import Utils from "../../lib/utils.js";
import PoseChangeTool from "./pose-change-tool.js";

export default class EResizeTool extends PoseChangeTool {
    constructor(f, n, b) {
        super(f, n, b, 'e-resize');
    }

    get isIllegalData() {
        return this.ex - this.sx == 0;
    }

    activeMove(x, y, l, t, r, b, dx, dy) {
        super.activeMove(x, y, l, t, r, b, dx, dy);
        if (!this.transformFeedback) return;
        let dis = Utils.distance(this.lx, this.ly, x, y);
        if (dx == 0) dis = 0;
        if (dx < 0) dis *= -1;
        this.transformFeedback.width += dis;
        this.frontRoot.refresh();
    }
}